@import "d:/tobe/function";
.child2{
  float:left;
  width:getPercent(6);//width: 50%，这个因为没有减gutter，所以正好是50%
  text-align: center;
  }
* {
  margin:0;padding:0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html, body{
  height: 100%;
}
.wrap-page {
    min-height: 100%;
  -webkit-overflow-scrolling: touch;
  background:#af1e23 url(../images/sz-hong-bg.png) no-repeat center center;
  background-size: 100% auto;
  text-align: center;
display: table;table-layout: fixed;width: 100%;
}
.page{display: table-cell;height:100vh;width: 100vw;vertical-align: middle;}
.inner{margin:0 0.78125rem;
}
.sz-hong-logo{width: 6.875rem;margin:0 auto 0.65625rem auto;}
.center-box{position: relative;height:6.625rem;margin-bottom: 0.78125rem}
.left-lian{position: absolute;top:0;left:0;width: 1.5625rem}
.right-lian{position: absolute;top:0;right:0;width: 1.5625rem}
.loading{position: absolute;top:0;left:50%;width: 3.5rem;margin-left:-1.75rem;}
.qiang-btn{position: absolute;top:3.5rem;left:50%;width: 2.46875rem;margin-left: -1.234375rem;}
.hello-out{border:1px solid #f2d08a;@include border-radius(5px);padding:0.390625rem;}
.hello-inner{border:1px dotted #f2d08a;@include border-radius(5px);padding:0.390625rem;font-size: 0.46875rem;color:#f7e289;line-height: 0.75rem;position: relative;}
.car{width: 1.03125rem;position:absolute;right:-0.53125rem;bottom:0.40625rem;}

/*6元优惠*/
.u-hongbao{width: 5.1875rem;margin:0 auto 0.390625rem auto;}
.zhufu,.jiang-type,.jiang-cash,.sz-btn{color:#f6e284;}
.zhufu{font-size: 0.78125rem;margin:0 auto 0.390625rem auto;font-weight: bold}
.jiang-type{font-size: 0.59375rem;margin:0 auto 0.390625rem auto;font-weight: bold}
.jiang-cash{font-size: 0.703125rem;font-weight: bold;margin-bottom: 0.390625rem}
.sz-btn{margin:auto; height:1.40625rem; width: 3.609375rem;display: block;font-size: 0.515625rem;line-height:1.40625rem;text-align: center;text-decoration: none;background: url(../images/sz-btn.png) no-repeat center center;background-size: 100% 100%;
}
.info-box{margin:0.3125rem 0;}

// mask
.mask{
  background:#af1e23 url(../images/sz-hong-bg.png) no-repeat center center;
  background-size: 100% auto;
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  display: table;table-layout: fixed;width: 100%;
  text-align: center;
}
.ipt{@include border-radius(5px);background: rgba(246,226,132,.8);border:1px solid #f6e284;outline:none;padding:0.234375rem;
    width: 7.03125rem;
    color: #af1e23;
    font-size:0.4375rem;
    -webkit-appearance: none;    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    -webkit-transition: all .3s linear;
    transition: all .3s linear;margin:0.3125rem auto 0.625rem auto}
